import {Route, Switch} from 'react-router-dom';

import {HomePage, SkillsPage, ProjectsPage} from '../pages';
import Sidebar from '../sidebar';

import './App.css';

function App() {
  return (
    <div className="App">
        <Sidebar/>
        <Switch>
            <Route exact path="/">
                <HomePage/>
            </Route>
            <Route path="/skills">
                <SkillsPage/>
            </Route>
            <Route path="/projects">
                <ProjectsPage/>
            </Route>
        </Switch>
    </div>
  );
}

export default App;
