import marketplace from './marketplace1.jpg';
import boost from './boost.jpg';
import zet from './zet.jpg';
import indicorp from './indicorp.jpg';
import digi from './digi.jpg';
import pulse from './pulse.jpg';


import './projectsPage.scss';

const ProjectsPage = () => {
    return (
        <article className="projects">
            <h2 className="projects__title">Проекты</h2>
            <ul className="projects__list">
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="../../projects/dist/index.html">
                        <img src={marketplace} alt=""/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="/">
                        <img src={boost} alt=""/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="https://www.facebook.com">
                        <img src={zet} alt=""/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="https://www.facebook.com">
                        <img src={indicorp} alt=""/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="https://www.facebook.com">
                        <img src={digi} alt=""/>
                    </a>
                </li>
                <li className="projects__item">
                    <a target="_blank" rel="noreferrer" href="https://www.facebook.com">
                        <img src={pulse} alt=""/>
                    </a>
                </li>
            </ul>
        </article>
    );
};

export default ProjectsPage;