import {NavLink} from 'react-router-dom';

import './sidebar.scss';

import homeSvg from './icons/home.svg';
import skillsSvg from './icons/skills.svg';
import projectsSvg from './icons/projects.svg';
import telegramSvg from './icons/telegram.svg';
import vkSvg from './icons/vk.svg';
import gmailSvg from './icons/gmail.svg';
import person from './686826446912.jpg'

const Sidebar = () => {
    return (
        <aside className="sidebar">
            <div className="sidebar__profile">
                <div className="sidebar__img-wrapper">
                    <img className="sidebar__img" src={person} alt="Руслн Волков"/>
                </div>
                <h2 className="sidebar__name">Руслан Волков</h2>
            </div>
            <nav className="sidebar__nav">
                <ul className="sidebar__links-list">
                    <li>
                        <NavLink exact className="sidebar__link" to="/" activeClassName="sidebar__link--active">
                            <img src={homeSvg} alt="home icon"/>
                            <p>Главная</p>
                        </NavLink>
                    </li>
                    <li>
                        <NavLink className="sidebar__link" to="/skills" activeClassName="sidebar__link--active">
                            <img src={skillsSvg} alt="skills icon"/>
                            <p>Навыки</p>
                        </NavLink>
                    </li>
                    <li>
                        <NavLink className="sidebar__link" to="/projects" activeClassName="sidebar__link--active">
                            <img src={projectsSvg} alt="projects icon"/>
                            <p>Проекты</p>
                        </NavLink>
                    </li>
                </ul>
            </nav>
            <nav className="sidebar__socials">
                <ul className="sidebar__social-list">
                    <li>
                        <a target="_blank" rel="noreferrer" href="https://telegram.org/">
                            <img src={telegramSvg} alt=""/>
                        </a>
                    </li>
                    <li>
                        <a target="_blank" rel="noreferrer" href="https://vk.com/board167809706">
                            <img src={vkSvg} alt=""/>
                        </a>
                    </li>
                    <li>
                        <a target="_blank" rel="noreferrer" href="https://mail.google.com/mail/u/0/#inbox">
                            <img src={gmailSvg} alt=""/>
                        </a>
                    </li>
                </ul>
            </nav>
        </aside>
    );
};

export default Sidebar;